import { Component, OnInit, Input } from '@angular/core';
import { WellKnownRoutes } from '@core';

import {
  Company, HouseholdFilers, HouseholdFilersStatus, CompanyTax
} from '@model';

import { getHouseholdFilerDisplayVm, HouseholdFilerDisplayVm } from '@utils/household-filer-utils';
import { StateAndLocalTaxSummary } from './review-container.component';


@Component({
  selector: 'sp-review-company-data',
  templateUrl: './review-company-data.component.html'
})
export class ReviewCompanyDataComponent implements OnInit {
  @Input() company: Company;
  @Input() stateAndLocalTaxSummaries: StateAndLocalTaxSummary[];
  @Input() futaTax: CompanyTax;
  @Input() householdFilers: HouseholdFilers;

  HouseholdFilersStatus = HouseholdFilersStatus;
  householdFilersVm: HouseholdFilerDisplayVm;

  bankAccountLink = WellKnownRoutes.BankAccount;
  companyLink = WellKnownRoutes.CompanyInfo;
  companyTaxLink = WellKnownRoutes.CompanyTaxInfo;
  fedTaxLink = WellKnownRoutes.CompanyTaxInfo;

  localTaxesTaxSummaries: StateAndLocalTaxSummary[] = [];

  ngOnInit() {
    this.householdFilersVm = getHouseholdFilerDisplayVm(this.householdFilers);
    this.localTaxesTaxSummaries = this.stateAndLocalTaxSummaries.filter(t => t.localTaxes && t.localTaxes.length > 0);
  }

}
