import { Component, ElementRef, ViewChild, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';

import { first } from 'rxjs/operators';

import { AccountStatus, Company } from '@model';
import { CompanyService } from '@entity';
import { SessionService } from '@session';
import { WellKnownRoutes } from '@core';

@Component({
  selector: 'sp-review-complete',
  templateUrl: './review-complete.component.html'
})
export class ReviewCompleteComponent implements OnInit {
  @ViewChild('confirmReadyContent')
  confirmReadyContent: ElementRef;
  @Input() company: Company;
  canApprove: boolean;

  constructor(
    private companyService: CompanyService,
    private modalService: NgbModal,
    private router: Router,
    private sessionService: SessionService,
  )   {}

  ngOnInit(): void {
    this.sessionService.isAdmin$.pipe(
      first()).subscribe(isAdmin => {
        this.canApprove = !isAdmin && !this.company.hasPriorPayroll;
      });
  }

  confirmReady() {
    this.sessionService.setAccountStatus(AccountStatus.Docs).subscribe(
      _ => this.router.navigate([WellKnownRoutes.Documents]),
      err => {
        // TODO: Something with error
      }
    );
  }

  approveCompanyData() {
    this.companyService.createCompanyEvent('CompanyDataVerificationApproved').subscribe(
      _ => this.confirmReady(),
      err => {
        this.sessionService.logger.error('Approval failed');
        // TODO: Something with error
      }
    );
  }
}
