import { Component, Input, OnChanges, OnInit } from '@angular/core';

import { Employee } from '@model';
import { WellKnownRoutes } from '@core';

@Component({
  selector: 'sp-review-validation',
  templateUrl: './review-validation.component.html'
})
export class ReviewValidationComponent {
  @Input() aboutYouErrors: string[] = [];
  @Input() bankInfoErrors: string[] = [];
  @Input() companyInfoErrors: string[] = [];
  @Input() companyTaxErrors: string[] = [];
  @Input() employeesErrors: string[] = [];
  @Input() employeeSetupErrors: string[] = [];
  @Input() doneAddingEmployees = false;
  @Input() returnTo: { label: string, link: string };

  aboutYouLink = [WellKnownRoutes.AboutYou, { forceValidationFeedback: true }];
  bankInfoLink = [WellKnownRoutes.BankAccount, { forceValidationFeedback: true }];
  companyInfoLink = [WellKnownRoutes.CompanyInfo, { forceValidationFeedback: true }];
  companyTaxInfoLink = [WellKnownRoutes.CompanyTaxInfo, { forceValidationFeedback: true }];
  employeesLink = [WellKnownRoutes.Employees, { forceValidationFeedback: true }];

  detailsLink = WellKnownRoutes.AccountView;

  getEeLink(emp: Employee) {
    return [WellKnownRoutes.Employees, { forceValidationFeedback: true }, emp.id];
  }

  getTaxLink(key: string) {
    return key === 'Federal'
      ? [WellKnownRoutes.CompanyTaxInfo, { forceValidationFeedback: true }]
      : [WellKnownRoutes.CompanyTaxInfo, { forceValidationFeedback: true }, 'state', key];
  }
}
