import { Component, OnInit, Input } from '@angular/core';
import { Employee, Address, EmployeePaymentMethod, PaymentAccountType, PaymentStrategy, TaxExemptionReasonsCodes } from '@model';
import { EntitySelectors } from '@entity';
import { getFullName } from '@core/utils/ee-utils';
import { WellKnownRoutes } from '@core';
import { EmployeeTax } from '@app/model/entities';
import { Observable } from 'rxjs';
import { map, tap } from 'rxjs/operators';

export class EmployeeTaxSummary {
  constructor(public name: string) {}
  isExempt = false;
}

@Component({
  selector: 'sp-review-employee-data',
  templateUrl: './review-employee-data.component.html'
})
export class ReviewEmployeeDataComponent implements OnInit {
  @Input()
  employee: Employee;
  homeAddress$: Observable<Address>;
  workAddress: Address;
  PrintedCheck = PaymentAccountType.PrintedCheck;
  PaymentAccountType = PaymentAccountType;
  pm: EmployeePaymentMethod;
  taxSummaries$: Observable<EmployeeTax[]>;

  suiTaxTermination = '0000-010';
  fedSuiPrefix = 'FE';

  constructor(private entitySelectors: EntitySelectors) { }

  ngOnInit(): void {

    this.homeAddress$ = this.entitySelectors.address.entities$.pipe(
      map(addresses => addresses.filter(ar => ar.id === this.employee.homeAddressId).shift())
    );

    this.entitySelectors.employeePaymentMethod.entities$.subscribe(ar => {
      this.pm = ar.find(p => p.employeeId === this.employee.id);
    });

    this.taxSummaries$ = this.entitySelectors.employeeTax.entities$.pipe(
      map(taxes => taxes.filter(t => !t.isRemoved
        && t.employeeId === this.employee.id
        && t.mtsTaxCode.endsWith(this.suiTaxTermination)))
    );

  }

  renderAccountType(accountType: PaymentAccountType): string {
    return accountType !== PaymentAccountType.PrintedCheck ? 'Account' : '';
  }

  getFullName = getFullName;

  getHireStatus() {
    if (!this.employee) {
      return;
    }
    if (this.employee.isNewHire) {
      return 'New Hire';
    }
    if (this.employee.isTerminated) {
      return 'Terminated';
    }
    return 'Active';
  }

  getFilingStatus(filingStatus: string) {
    switch (filingStatus) {
      case 'S': {
        return 'Single';
      }
      case 'M': {
        return 'Married';
      }
      case 'H': {
        return 'Head of Household';
      }
    }
  }

  getFriendlyName(tax: EmployeeTax) {
    if (tax.mtsTaxCode.endsWith(this.suiTaxTermination)) {
      if (tax.mtsTaxCode.startsWith(this.fedSuiPrefix)) {
        return 'Federal Unemployment Status';
      } else {
        return tax.stateCode + ' State Unemployment Status';
      }
    } else {
      return tax.name;
    }
  }
  getExemptReason(exemptReasonCode: string) {
    return TaxExemptionReasonsCodes.getDisplayName(exemptReasonCode);
  }

  getSplitAmount(val: number) {
    if (this.pm.strategy === PaymentStrategy.SplitAmount) {
      return `$ ${val}`;
    }

    if (this.pm.strategy === PaymentStrategy.SplitPercent) {
      return `${val} %`;
    }
  }

  getEeLink(emp: Employee) {
    return WellKnownRoutes.Employees + '/' + emp.id;
  }
}
