import { Component, Input, OnInit } from '@angular/core';

import { getFullName, WellKnownRoutes } from '@core';

import {
  Address, Company, Employee,
  HouseholdFilers, HouseholdFilersStatus, CompanyTax
} from '@model';
import { getHouseholdFilerDisplayVm, HouseholdFilerDisplayVm } from '@utils/household-filer-utils';
import { StateAndLocalTaxSummary } from './review-container.component';

@Component({
  selector: 'sp-review-data',
  templateUrl: './review-data.component.html'
})
export class ReviewDataComponent implements OnInit {
  @Input() company: Company;
  @Input() employees: Employee[];
  @Input() householdFilers: HouseholdFilers;
  @Input() stateAndLocalTaxSummaries: StateAndLocalTaxSummary[];
  @Input() workLocations: Address[];
  @Input() futaTax: CompanyTax;

  getFullName = getFullName;
  HouseholdFilersStatus = HouseholdFilersStatus;
  householdFilersVm: HouseholdFilerDisplayVm;

  detailsLink = WellKnownRoutes.AccountView;

  ngOnInit() {
    this.householdFilersVm = getHouseholdFilerDisplayVm(this.householdFilers);
  }
}
