import { Component, OnInit } from '@angular/core';
import { combineLatest, Observable } from 'rxjs';
import { filter, map, scan, withLatestFrom } from 'rxjs/operators';
import {
  AddressService,
  CompanyTaxService,
  EmployeeService,
  HouseholdFilersService,
  ReviewService
} from '@entity/entity-services';

import { AccountStatus, Address, Company, CompanyTax, Employee, HouseholdFilers, TaxTypeCode } from '@model';
import { getFullName, Indexable, WellKnownRoutes } from '@core';
import { getEmployeeId } from '@entity';
import { SessionService } from '@session';
import { ValidationError } from '@validation';

 /** Summary of company state taxes for review  */
export class StateAndLocalTaxSummary {
  constructor(public state: string) {}

  swhFound = false;
  swhEin = '';

  suiFound = false;
  suiExempt = false;
  suiEin = '';
  suiRate: number;

  localTaxes: LocalTaxSummary[] = [];
}

export class LocalTaxSummary {
  constructor(public localTaxEin: string, public localTaxAuthority: string, public isExempt: boolean) {}
}

@Component({
  selector: 'sp-review-container',
  templateUrl: './review-container.component.html'
})
export class ReviewContainerComponent implements OnInit {
  aboutYouErrors$: Observable<string[]>;
  bankInfoErrors$: Observable<string[]>;
  companyInfoErrors$: Observable<string[]>;
  companyTaxErrors$: Observable<{ key: string; messages: string[] }[]>;
  employeesErrors$: Observable<{ employee: Employee; messages: string[] }[]>;
  employeeSetupErrors$: Observable<string[]>;
  isValid$: Observable<boolean>;

  company$: Observable<Company>;
  employees$: Observable<Employee[]>;
  householdFilers$: Observable<HouseholdFilers>;
  returnTo$: Observable<{ label: string, link: string }>;
  stateAndLocalTaxSummaries$: Observable<StateAndLocalTaxSummary[]>;
  futaTax$: Observable<CompanyTax>;
  workLocations$: Observable<Address[]>;

   // TODO: replace once tax.categoryCode is present
   swhTaxTermination = '0000-001';
   suiTaxTermination = '0000-010';

  constructor(
    private addressService: AddressService,
    private companyTaxService: CompanyTaxService,
    private employeeService: EmployeeService,
    private householdFilersService: HouseholdFilersService,
    private reviewService: ReviewService,
    private sessionService: SessionService
  ) {}

  ngOnInit(): void {
    window.scrollTo(0, 0);

    this.returnTo$ = combineLatest(
      this.sessionService.currentCompany$,
      this.sessionService.isAdmin$,
      ).pipe(
      filter(([company]) => !!company), // TODO: Unnecessary after PR #506
      map(([company, isAdmin]) => {
        if (company.status === AccountStatus.Open || company.status === AccountStatus.Test) {
          return { label: 'Return to Dashboard', link: WellKnownRoutes.Dashboard };
        } else {
          return {
            label: 'Return to AccountView',
            link: isAdmin ? WellKnownRoutes.AdminAccountView : WellKnownRoutes.AccountView
          };
        }
      })
    );

    this.isValid$ = this.reviewService.isValid$;
    this.dataReview();
    this.validationReview();
  }

  private dataReview() {
    this.company$ = this.sessionService.currentCompany$;

    this.stateAndLocalTaxSummaries$ = this.companyTaxService.stateAndLocalTaxes$.pipe(
      map(taxes => taxes.filter(t => !t.isRemoved)),
      map(taxes => {

        const summaries = taxes.reduce((acc, t) => {

          if (t.taxTypeCode === TaxTypeCode.State) {
            if (t.mtsTaxCode.endsWith(this.swhTaxTermination)) {
              const sum = getSummary(t.stateCode);
              sum.swhFound = true;
              sum.swhEin = t.appliedForEin ? 'APPLIED FOR' : t.ein;
            }

            if (t.mtsTaxCode.endsWith(this.suiTaxTermination)) {
              const sum = getSummary(t.stateCode);
              sum.suiFound = true;
              sum.suiExempt = t.isExempt;
              sum.suiEin = t.appliedForEin ? 'APPLIED FOR' : t.ein;
              sum.suiRate = t.rate;
            }
          } else {
            const sum = getSummary(t.stateCode);
            sum.localTaxes.push(new LocalTaxSummary(t.appliedForEin ? 'APPLIED FOR' : t.ein, t.name, t.isExempt));
          }

          return acc;

          function getSummary(state: string): StateAndLocalTaxSummary {
            return acc[state] || (acc[state] = new StateAndLocalTaxSummary(state));
          }
        }, {} as { [state: string]: StateAndLocalTaxSummary });
        return Object.keys(summaries).map(st => summaries[st]);
      })
    );

    this.futaTax$ = this.companyTaxService.federalTaxes$.pipe(
      map(taxes => taxes.find(t => t.mtsTaxCode.endsWith(this.suiTaxTermination)))
    );

    this.employees$ = this.employeeService.entities$.pipe(
      map(employees => employees.filter(emp => !emp.isRemoved))
    );

    this.workLocations$ = this.addressService.workAddresses$;

    this.householdFilers$ = this.householdFilersService.householdFilers$;
  }

  private validationReview(): void {

    this.aboutYouErrors$ = this.reviewService.aboutYouErrors$.pipe(
      map(ReviewContainerComponent.getMessages)
    );

    this.bankInfoErrors$ = this.reviewService.bankInfoErrors$.pipe(
      map(ReviewContainerComponent.getMessages)
    );

    this.companyInfoErrors$ = this.reviewService.companyInfoErrors$.pipe(
      map(ReviewContainerComponent.getMessages)
    );

    this.companyTaxErrors$ = this.reviewService.companyTaxErrors$.pipe(
      map(validationErrors => {
        const groups = validationErrors.reduce(
          (g, error) => {
            // For missing state taxes, push error to each missing state key
            if (error.context.missingTaxStates) {
              error.context.missingTaxStates.forEach((state: string) => {
                (g[state] = g[state] || []).push(error);
              });
              return g;
            }

            const key = error.validator.group === 'fedTax' ? 'Federal' : error.context.model.state;
            (g[key] = g[key] || []).push(error);
            return g;
          },
          {} as Indexable
        );
        return Object.keys(groups)
          .map(key => {
            return {
              key,
              messages: ReviewContainerComponent.getMessages(groups[key])
            };
          })
          .sort((a, b) =>
            // Sort 'Federal' first, then states alphabetically
            (a.key === 'Federal' ? '' : a.key).localeCompare(b.key === 'Federal' ? '' : b.key)
          );
      })
    );

    this.employeesErrors$ = this.reviewService.employeesErrors$.pipe(
      map(ves => ves.filter(ve => ve.validator.group !== 'employeeSetup')),
      withLatestFrom(this.employeeService.entities$),
      map(([validationErrors, employees]) => {
        const groups = validationErrors
          .reduce((g, error) => {
            if (error.validator.group === 'employeeSetup') {
              return g;
            }
            const employeeId = getEmployeeId(error.context, employees);
            if (employeeId) {
              (g[employeeId] = g[employeeId] || []).push(error);
            }
            return g;
          },
          {} as Indexable
        );
        return Object.keys(groups)
          .map(employeeId => {
            const employee = employees.find(emp => emp.id === employeeId);
            return {
              employee,
              messages: ReviewContainerComponent.getMessages(groups[employeeId])
            };
          })
          .sort((a, b) => getFullName(a.employee).localeCompare(getFullName(b.employee)));
      })
    );

    this.employeeSetupErrors$ = this.reviewService.employeesErrors$.pipe(
      map(ves => ves.filter(ve => ve.validator.group === 'employeeSetup')),
      map(ReviewContainerComponent.getMessages)
    );
  }

  private static getMessages(validationErrors: ValidationError[]) {
    return validationErrors
      .map(validationError => validationError.getErrorMessage());
  }
}
