import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { SharedModule } from '@shared/shared.module';

import { AccountDetailsModule } from '@app/account-details';
import { ReviewCompleteComponent } from './review-complete.component';
import { ReviewContainerComponent } from './review-container.component';
import { ReviewDataComponent } from './review-data.component';
import { ReviewValidationComponent } from './review-validation.component';
import { ReviewEmployeeDataComponent } from './review-employee-data.component';
import { ReviewCompanyDataComponent } from './review-company-data.component';

const routes: Routes = [
  { path: '', pathMatch: 'full', component: ReviewContainerComponent }
];

@NgModule({
  imports: [SharedModule, AccountDetailsModule, RouterModule.forChild(routes)],
  declarations: [
    ReviewCompleteComponent,
    ReviewContainerComponent,
    ReviewDataComponent,
    ReviewEmployeeDataComponent,
    ReviewCompanyDataComponent,
    ReviewValidationComponent,
  ]
})
export class ReviewModule {}
